.contacts {
  display: flex;
  justify-content: center;
  margin: 0;
  margin-bottom: 29px;
  padding: 0;

  list-style-type: none;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.contacts__item {
  width: 48px;
  height: 48px;

  background-color: $blue;
}

.contacts__item:not(:last-child) {
  margin-right: 6px;
}

.contacts__link {
  display: block;
  height: 100%;
  margin: 0;
  padding: 0;

  color: $black;
  text-decoration: none;

  cursor: pointer;
}

.copyright {
  margin: 0;
  padding: 0;

  color: $black;
  text-decoration: none;

  cursor: pointer;
}
